.card{
	margin-top: 20px;
}
body{
	max-width: 100%;
}
header.header{
	max-width: 100%;
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
	body{
		font-size: 14px;
	};
	form.needs-validation{
		font-size: 12px;
		background-color: black;
	}
	/*.container{
		margin: 0 auto;
		padding: 0 10px;
		max-width: 640px;
		font-size: 12px;
	}*/
}

/* Portrait phones and smaller */
@media (max-width: 480px) {

	form.input-group{
		font-size: 12px;
		flex-wrap: wrap;
		max-width: 400px;
	}
	/*.container{
		margin: 0 auto;
		padding: 0 2px;
		width: 100%;
		max-width: 450px;
		font-size: 12px;
	}*/
	.btn{
		font-size: 10px;
		padding: 2px 10px;
	}
	table.table{
		min-width: 300px;
		font-size: 10px;
	}
}